import { Ionicons } from '@expo/vector-icons';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Alert, Linking, SafeAreaView, ScrollView, StyleSheet, Switch, Text, TouchableOpacity, View } from 'react-native';
import { clearExamHistory, clearMistakes, clearStudyProgress, resetDatabase } from '../utils/database';
import { fixAllIssues } from '../utils/fixFavorites';
import { runAllFixes } from '../utils/fixJson';

// 
const APP_INFO = {
  version: '1.0.0',
  author: '',
  copyright: ' 2025 ',
  website: 'https://driving.example.com'
};

// 
const LANGUAGES = [
  { code: 'zh', name: '', flag: '' },
  { code: 'en', name: 'English', flag: '' },
  { code: 'cs', name: 'etina', flag: '' },
  { code: 'es', name: 'Espaol', flag: '' },
];

const SettingsScreen = ({ navigation }) => {
  const { t, i18n } = useTranslation();
  const [selectedLanguage, setSelectedLanguage] = useState(i18n.language);
  const [notificationsEnabled, setNotificationsEnabled] = useState(false);
  const [darkModeEnabled, setDarkModeEnabled] = useState(false);

  useEffect(() => {
    // 
    const loadLanguageSettings = async () => {
      try {
        const savedLanguage = await AsyncStorage.getItem('user-language');
        if (savedLanguage) {
          setSelectedLanguage(savedLanguage);
        }
      } catch (error) {
        console.error('Error loading language settings:', error);
      }
    };

    loadLanguageSettings();
  }, []);

  // 
  useEffect(() => {
    const loadSettings = async () => {
      try {
        const notificationsValue = await AsyncStorage.getItem('notificationsEnabled');
        setNotificationsEnabled(notificationsValue === 'true');
        
        const darkModeValue = await AsyncStorage.getItem('darkModeEnabled');
        setDarkModeEnabled(darkModeValue === 'true');
      } catch (error) {
        console.error('Failed to load settings', error);
      }
    };
    
    loadSettings();
  }, []);

  // 
  const changeLanguage = async (langCode) => {
    try {
      await AsyncStorage.setItem('user-language', langCode);
      i18n.changeLanguage(langCode);
      setSelectedLanguage(langCode);
      
      // 
      Alert.alert(
        '',
        '',
        [{ text: '', style: 'default' }]
      );
    } catch (error) {
      console.error('Error saving language setting:', error);
    }
  };

  // 
  const toggleNotifications = async (value) => {
    try {
      await AsyncStorage.setItem('notificationsEnabled', value.toString());
      setNotificationsEnabled(value);
      
      if (value) {
        Alert.alert('', '');
      } else {
        Alert.alert('', '');
      }
    } catch (error) {
      console.error('Failed to save notification setting', error);
    }
  };
  
  // 
  const toggleDarkMode = async (value) => {
    try {
      await AsyncStorage.setItem('darkModeEnabled', value.toString());
      setDarkModeEnabled(value);
      
      Alert.alert(
        value ? '' : '',
        value ? '' : ''
      );
    } catch (error) {
      console.error('Failed to save dark mode setting', error);
    }
  };

  // 
  const clearFavorites = async () => {
    try {
      await AsyncStorage.setItem('favorites', JSON.stringify([]));
      Alert.alert('', '');
    } catch (error) {
      console.error('Failed to clear favorites:', error);
      Alert.alert('', '');
    }
  };
  
  // 
  const clearMistakeRecords = async () => {
    try {
      await clearMistakes();
      Alert.alert('', '');
    } catch (error) {
      console.error('Failed to clear mistakes:', error);
      Alert.alert('', '');
    }
  };
  
  // 
  const clearExamRecords = async () => {
    try {
      await clearExamHistory();
      await clearStudyProgress();
      Alert.alert('', '');
    } catch (error) {
      console.error('Failed to clear exam history:', error);
      Alert.alert('', '');
    }
  };

  // 
  const handleResetData = () => {
    Alert.alert(
      t('settings.resetData'),
      t('settings.resetConfirm'),
      [
        {
          text: t('common.cancel'),
          style: 'cancel'
        },
        {
          text: t('common.confirm'),
          style: 'destructive',
          onPress: async () => {
            try {
              // 
              await resetDatabase();
              // 
              await clearStudyProgress();
              await clearExamHistory();
              
              Alert.alert(t('common.success'), '');
            } catch (error) {
              console.error('Error resetting data:', error);
              Alert.alert(t('common.error'), error.message);
            }
          }
        }
      ]
    );
  };
  
  // 
  const showAppInfo = () => {
    Alert.alert(
      '',
      `\n: ${APP_INFO.version}\n${APP_INFO.copyright}\n\n`,
      [{ text: '', style: 'default' }]
    );
  };
  
  // 
  const goToRating = () => {
    Alert.alert(
      '',
      '',
      [
        { text: '', style: 'cancel' },
        { text: '', onPress: () => {
          // 
          Linking.openURL('https://play.google.com/store');
        }}
      ]
    );
  };
  
  // 
  const viewPrivacyPolicy = () => {
    Alert.alert(
      '',
      '',
      [
        { text: '', style: 'cancel' },
        { text: '', onPress: () => {
          // 
          Linking.openURL('https://example.com/privacy');
        }}
      ]
    );
  };

  // 
  const handleFixApp = () => {
    Alert.alert(
      '',
      'JSON',
      [
        { text: '', style: 'cancel' },
        {
          text: '',
          style: 'default',
          onPress: async () => {
            try {
              // 
              await Promise.all([
                fixAllIssues(), // 
                runAllFixes()   // JSON
              ]);
              
              // 
              Alert.alert('', '');
            } catch (error) {
              console.error(':', error);
              Alert.alert('', `: ${error.message}`);
            }
          }
        }
      ]
    );
  };

  // 
  const renderLanguageOption = (language) => {
    const isSelected = selectedLanguage === language.code;
    
    return (
      <TouchableOpacity 
        key={language.code} 
        style={[styles.settingOption, isSelected && styles.selectedOption]}
        onPress={() => changeLanguage(language.code)}
      >
        <View style={styles.languageInfo}>
          <Text style={styles.languageFlag}>{language.flag}</Text>
          <Text style={styles.languageName}>{language.name}</Text>
        </View>
        {isSelected && <Ionicons name="checkmark-circle" size={24} color="#2196F3" />}
      </TouchableOpacity>
    );
  };

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView contentContainerStyle={styles.scrollContainer}>
        <View style={styles.header}>
          <Text style={styles.title}>{t('settings.title')}</Text>
        </View>
        
        <View style={styles.section}>
          <Text style={styles.sectionTitle}>{t('settings.language')}</Text>
          <View style={styles.sectionContent}>
            {LANGUAGES.map(language => renderLanguageOption(language))}
          </View>
        </View>
        
        <View style={styles.section}>
          <Text style={styles.sectionTitle}></Text>
          <View style={styles.sectionContent}>
            <View style={styles.settingItem}>
              <View style={styles.settingInfo}>
                <Ionicons name="notifications-outline" size={24} color="#555" style={styles.settingIcon} />
                <View>
                  <Text style={styles.settingTitle}></Text>
                  <Text style={styles.settingDescription}></Text>
                </View>
              </View>
              <Switch 
                value={notificationsEnabled}
                onValueChange={toggleNotifications}
                trackColor={{ false: '#d9d9d9', true: '#4CAF50' }}
                thumbColor={notificationsEnabled ? '#fff' : '#f4f3f4'}
              />
            </View>
            
            <View style={styles.settingItem}>
              <View style={styles.settingInfo}>
                <Ionicons name="moon-outline" size={24} color="#555" style={styles.settingIcon} />
                <View>
                  <Text style={styles.settingTitle}></Text>
                  <Text style={styles.settingDescription}></Text>
                </View>
              </View>
              <Switch 
                value={darkModeEnabled}
                onValueChange={toggleDarkMode}
                trackColor={{ false: '#d9d9d9', true: '#0066cc' }}
                thumbColor={darkModeEnabled ? '#fff' : '#f4f3f4'}
              />
            </View>
          </View>
        </View>
        
        <View style={styles.section}>
          <Text style={styles.sectionTitle}></Text>
          <View style={styles.sectionContent}>
            <TouchableOpacity style={styles.optionItem}>
              <View style={styles.settingInfo}>
                <Ionicons name="information-circle-outline" size={24} color="#555" style={styles.settingIcon} />
                <Text style={styles.settingTitle}></Text>
              </View>
              <Ionicons name="chevron-forward" size={20} color="#ccc" />
            </TouchableOpacity>
            
            <TouchableOpacity style={styles.optionItem}>
              <View style={styles.settingInfo}>
                <Ionicons name="star-outline" size={24} color="#555" style={styles.settingIcon} />
                <Text style={styles.settingTitle}></Text>
              </View>
              <Ionicons name="chevron-forward" size={20} color="#ccc" />
            </TouchableOpacity>
            
            <TouchableOpacity style={styles.optionItem}>
              <View style={styles.settingInfo}>
                <Ionicons name="lock-closed-outline" size={24} color="#555" style={styles.settingIcon} />
                <Text style={styles.settingTitle}></Text>
              </View>
              <Ionicons name="chevron-forward" size={20} color="#ccc" />
            </TouchableOpacity>
            
            <View style={styles.versionInfo}>
              <Text style={styles.versionText}> v1.0.0</Text>
            </View>
          </View>
        </View>
        
        <View style={styles.section}>
          <Text style={styles.sectionTitle}></Text>
          <View style={styles.sectionContent}>
            <TouchableOpacity style={styles.optionItem} onPress={handleFixApp}>
              <View style={styles.settingInfo}>
                <Ionicons name="construct-outline" size={24} color="#555" style={styles.settingIcon} />
                <Text style={styles.settingTitle}></Text>
              </View>
              <Ionicons name="chevron-forward" size={20} color="#ccc" />
            </TouchableOpacity>
          </View>
        </View>
        
        <View style={styles.dangerSection}>
          <TouchableOpacity style={styles.dangerButton} onPress={handleResetData}>
            <Text style={styles.dangerButtonText}>{t('settings.resetData')}</Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f8f9fa',
  },
  scrollContainer: {
    paddingBottom: 32,
  },
  header: {
    padding: 16,
    borderBottomWidth: 1,
    borderBottomColor: '#f0f0f0',
    backgroundColor: '#fff',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#2196F3',
  },
  section: {
    backgroundColor: '#fff',
    marginTop: 20,
    borderTopWidth: 1,
    borderBottomWidth: 1,
    borderColor: '#f0f0f0',
  },
  sectionTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#333',
    margin: 16,
  },
  sectionContent: {
    borderTopWidth: 1,
    borderTopColor: '#f0f0f0',
  },
  settingOption: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 16,
    borderBottomWidth: 1,
    borderBottomColor: '#f0f0f0',
  },
  selectedOption: {
    backgroundColor: '#E3F2FD',
  },
  optionText: {
    fontSize: 16,
    color: '#333',
  },
  infoRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 16,
    borderBottomWidth: 1,
    borderBottomColor: '#f0f0f0',
  },
  infoLabel: {
    fontSize: 16,
    color: '#333',
  },
  infoValue: {
    fontSize: 16,
    color: '#757575',
  },
  dangerSection: {
    padding: 16,
    marginTop: 20,
    alignItems: 'center',
  },
  dangerButton: {
    backgroundColor: '#F44336',
    paddingVertical: 12,
    paddingHorizontal: 24,
    borderRadius: 8,
  },
  dangerButtonText: {
    color: '#fff',
    fontSize: 16,
    fontWeight: '500',
  },
  languageInfo: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  languageFlag: {
    fontSize: 22,
    marginRight: 15,
  },
  languageName: {
    fontSize: 16,
    color: '#333',
  },
  settingItem: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: 15,
    borderBottomWidth: 1,
    borderBottomColor: '#f0f0f0',
  },
  settingInfo: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  settingIcon: {
    marginRight: 15,
  },
  settingTitle: {
    fontSize: 16,
    color: '#333',
  },
  settingDescription: {
    fontSize: 14,
    color: '#999',
    marginTop: 3,
  },
  versionInfo: {
    alignItems: 'center',
    paddingVertical: 20,
  },
  versionText: {
    fontSize: 14,
    color: '#999',
  }
});

export default SettingsScreen; 