/**
 * JSON
 * 
 * JSON
 */
import AsyncStorage from '@react-native-async-storage/async-storage';
import * as FileSystem from 'expo-file-system';
import { Alert, Platform } from 'react-native';

/**
 * JSON
 * @returns {Promise<boolean>} 
 */
export const fixJsonErrors = async () => {
  try {
    console.log('JSON...');
    
    // Web
    if (Platform.OS === 'web') {
      // WebfixedJSON
      await AsyncStorage.setItem('useFixedJson', 'true');
      console.log('WebfixedJSON');
      return true;
    }
    
    // 
    const hasFixedFiles = await checkAndCopyFixedFiles();
    
    if (hasFixedFiles) {
      Alert.alert(
        '',
        'JSON'
      );
      return true;
    } else {
      Alert.alert(
        '',
        'JSON'
      );
      return false;
    }
  } catch (error) {
    console.error('JSON:', error);
    Alert.alert(
      '',
      `JSON: ${error.message}`
    );
    return false;
  }
};

/**
 * 
 * @returns {Promise<boolean>} 
 */
const checkAndCopyFixedFiles = async () => {
  try {
    if (!FileSystem) {
      console.log('FileSystem API');
      return false;
    }
    
    // 
    const dataDir = FileSystem.documentDirectory + 'assets/data/';
    
    // 
    await FileSystem.makeDirectoryAsync(dataDir, { intermediates: true }).catch(() => {});
    
    // 
    const fixedFiles = [];
    for (let i = 0; i <= 5; i++) {
      const fixedFileName = `setofquestions${i}_fixed.json`;
      const fixedFilePath = `${dataDir}${fixedFileName}`;
      
      try {
        const info = await FileSystem.getInfoAsync(fixedFilePath);
        if (info.exists) {
          fixedFiles.push({
            setNumber: i,
            path: fixedFilePath
          });
        }
      } catch (e) {
        console.log(` ${fixedFileName} :`, e);
      }
    }
    
    console.log(` ${fixedFiles.length} `);
    
    // 
    if (fixedFiles.length === 0) {
      return false;
    }
    
    // 
    for (const file of fixedFiles) {
      const originalFilePath = `${dataDir}setofquestions${file.setNumber}.json`;
      
      // 
      const backupPath = `${dataDir}setofquestions${file.setNumber}_backup.json`;
      try {
        await FileSystem.copyAsync({
          from: originalFilePath,
          to: backupPath
        });
        console.log(`: ${originalFilePath} -> ${backupPath}`);
      } catch (e) {
        console.log(` ${originalFilePath} :`, e);
      }
      
      // 
      try {
        await FileSystem.copyAsync({
          from: file.path,
          to: originalFilePath
        });
        console.log(`: ${file.path} -> ${originalFilePath}`);
      } catch (e) {
        console.error(` ${originalFilePath} :`, e);
      }
    }
    
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

/**
 * 
 * @returns {Promise<boolean>} 
 */
export const runAllFixes = async () => {
  try {
    console.log('...');
    
    // JSON
    const jsonFixResult = await fixJsonErrors();
    console.log('JSON:', jsonFixResult);
    
    // 
    // ...
    
    return jsonFixResult;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

export default {
  fixJsonErrors,
  runAllFixes
}; 